.Control-Buttons {
margin: 3rem 0;
width: 100%;
box-sizing: border-box;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
flex-wrap: wrap;
}

.btn-grp {
display: flex;
align-items: center;
justify-content: space-around;
}

.btn-grp-process {
    display: flex;
    align-items: left;
    flex-direction: column;
    justify-content: space-around;
}

.btn-grp-process
{
    width: 100vw;
    margin-bottom: 5px;
}

.btn {
font-family: Verdana, Geneva, Tahoma, sans-serif;
width: 12vw;
height: 3em;
font-size: 1.3em;
border-radius: 14px;
margin: .5em 6px;
display: flex;
border: 2px solid #e42a2a;
justify-content: center;
align-items: center;
cursor: pointer;
color: #f5f5f5;
}


.btn-one{
    background-color: #e42a2a;
}

.btn-process {

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2%;
    width: 96%;
    border-radius: 0;
    border: 0; 
    background-color: #f8b600;
    color: #000;
    margin: .5em 0;
}

.info-process {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2%;
    width: 96%;
    border-radius: 0;
    border: 0; 
    color: #f5f5f5;
    margin: .5em 0;
}